export const NEON_HISTORY = `На сегодняшний день неоновые вывески, украшающие здания и магазины, никого не удивляют. Улицы подсвечены огнями – это воспринимается как будничное явление. Однако введение этого технического новшества буквально сто лет назад шокировало граждан. Первые неоновые вывески были большой редкостью и встречались только в богатых районах городов.

Изобретение неоновой лампы было запатентовано в 1915 году. У открытия была интересная предыстория, которая уходила корнями в далекий 1675 год. Именно тогда французский астроном Жан Пикар увидел чуть заметное свечение в трубке ртутного барометра. Это явление получило название «барометрического» свечения. Разумеется, тогда об электричестве ничего не было известно, поэтому ученый не смог объяснить происходящее. Но это событие послужило поводом для начала масштабных исследований загадочного света. В 1855 году произошел прорыв в этой области. Генрих Гейслер, немецкий физик, сделал трубку, которая стала предшественницей газоразрядных ламп. Так началась история неоновой лампы.

Это изобретение мгновенно распространилось и в разных концах мира начали проводить эксперименты. Внутрь прозрачной трубки закачали газ, и под воздействием электричества она стала светиться!`;

export const FURNITURE_HISTORY =
  "Покупая мебель, мы не всегда задумываемся о том, к какой категории ее отнести, и кто ее создал. Но только до того момента, пока на глаза попадается вещь, не вписывающаяся в рамки привычного. Необычный предмет интерьера запоминается и вызывает к себе интерес. Обычно дизайнерская мебель вызывает тот самый повышенный интерес. В том числе, к личности ее создателя.";

export const VOL_PRINTERS_HISTORY =
  'Хоть и 3D-печать - относительно новая технология, но ее история глубока, разнообразна и интересна - и, конечно, продолжает развиваться до сих пор. 3D-принтер появился в 1981 году, когда доктор Хидео Кодама изобрел одну из первых машин для быстрого прототипирования, которая создавала детали слой за слоем, используя смолу, полимеризуемую под воздействием ультрафиолетового света. В 1986 году первый патент на стереолитографию (SLA) был подан Чаком Халлом, который считается "изобретателем 3D-печати" за создание и коммерциализацию SLA и формата .stl - наиболее распространенного типа файлов, используемого для 3D-печати.';
